header{
    width: 100%;
    height: 100vh;
    position: relative;
    .bg("../img/StarWarsBg.jpg");
    .menu_top{
        padding: 20px 0;
        background-color: rgba(16, 22, 54, .5);
        .navbar{
            display: flex;
            justify-content: space-between;
            .logo{
                color: @logo;
                font-size: 20px;
                font-weight: 900;
                transition: 500ms;
                &:hover{
                  color: white;
                  text-shadow: 0 0 20px blue;  
                }
                .blue{
                    color: @blue1;
                }
            }
        }
        .header_menu{
            display: flex;
            &.active_header{
                transform: translateX(0) !important;
            }
            .md-block({
                position: fixed;
                top: 20%;
                right: 0;
                background-color: rgba(16, 22, 54, .5);
                width: 40%;
                height: 50%;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                z-index: 50;
                transform: translateX(300px);
                //transition: .5s;
                border-radius: 10px
            });
            #close{
                font-size: 20px;
                color: white;
                top: 5%;
                right: 5%;
                cursor: pointer;
                .fal{
                    color: red;
                }
            }
            .header_item{
                .md-block({
                    margin: 20px 0;
                });
                margin: 0 15px;
                .header_link{
                    color: @white;
                    font-size: 14px;
                    font-weight: 400;
                    text-decoration: none;
                    transition: 500ms;
                    &.active_link{
                        color: red !important;
                        &::before{
                            background: red;
                        }
                    }
                    &::before{
                        content: "";
                        display: block;
                        width: 100%;
                        height: 2px;
                        background: @blue1;
                        position: relative;
                        bottom: 20px;
                        opacity: 0;
                        transition: 500ms;
                        .md(display, none)
                    }                    
                    &:hover{
                        color: @blue1;
                        text-decoration: underline;
                        &::before{
                            opacity: 1;
                        }
                    }
                }
            }
        }
        .burger{
            background: transparent;
            border: 2px solid black;
            cursor: pointer;
            border-radius: 5px;
            box-shadow: 0 0 15px yellowgreen;
            display: none;
            z-index: 51;
            .md(display, block);
            .far{
                color: white;
                font-size: 20px;
            }
        }
    }
    .header_title{
        color: @white;
        text-align: center;
        position: absolute;
        width: 100%;
        top: 40%;
        h1{
            font-size: 62px;
            font-weight: 700;
            .blue_1{
                color: @blue2;
            }
            .blue_2{
                color: @blue1;
            }
            .lg(font-size, 55px);
            .md(font-size, 45px);

        }
        h2{
            color: @white;
            font-size: 48px;
            font-weight: 100;
            margin-top: 20px;
            .blue_2{
                color: @blue1;
            }
            .lg(font-size, 35px);
            .md(font-size, 25px);
        }
        p{
            font-size: 18px;
            font-weight: 100;
            margin-top: 20px;
            .sm(font-size, 15px);
            &::after{
                content: "";
                display: inline-block;
                width: 28px;
                height: 2px;
                background: @blue1;
                transform: translate(40px, -5px);
                .sm(transform, translate(20px, -5px));
          
            }
            &::before{
                content: "";
                display: inline-block;
                width: 28px;
                height: 2px;
                background: @blue1;
                transform: translate(-40px, -5px);
                .sm(transform, translate(-20px, -5px));

            }
        }
        .social_icons{
            display: flex;
            justify-content: center;
            margin-top: 100px;
            .md(margin-top, 10%);
            li{
                margin: 0 10px;
                a{
                    .fab{
                        color: @white;
                        font-size: 25px;
                        transition: 500ms;
                        opacity: .4;
                        &:hover{
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }
}