.works{
    background: linear-gradient(to right, white, #cba382);
    padding-top: 80px;
    text-align: center;
    .works_title{
        h2{
            margin-bottom: 20px;
        }
        p{
            color: @dark2;
            font-size: 14px;
            font-weight: 300;
            line-height: 18px;
        }
        .heart{
            margin-bottom: 40px;
            &::before{
                content: "";
                display: inline-block;
                width: 92px;
                height: 2px;
                background-color: #c5af9b;
                transform: translate(-15px, -7px);
            }
            &::after{
                content: "";
                display: inline-block;
                width: 92px;
                height: 2px;
                background-color: #c5af9b;
                transform: translate(15px, -7px);
            }
        }
    }
    .navs{
        display: flex;
        justify-content: center;
        margin: 50px 0 70px;
        li{
            margin: 0 20px;
            a{
                text-decoration: none;
                display: block;
                padding: 10px 20px;
                color: @dark2;
                font-size: 14px;
                font-weight: 900;
                &.nav_active{
                    color: white;
                    background-color: @blue1;
                    border-radius: 5px;
                }
            }
        }
    }
    .tab-item{
        position: relative;
        &:hover .overlay{
            opacity: 1;
        }
        .overlay{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(76, 118, 225, .9);
            display: flex;
            justify-content: center;
            align-items: center;
            opacity: 0;
            transition: .5s;
            cursor: pointer;
            .fal{
                color: white;
                font-size: 20px;
            }
        }
        img{
            width: 100%;
            height: 100%;
        }
    }
}