.feature{
    position: relative;
    z-index: -2;
    .bg('../img/StarWars_022.jpg');
    background-position: right;
    padding-top: 80px;
    padding-bottom: 120px;
    h2{
        margin-bottom: 100px;
        .sm(margin-bottom, 50px);
    }
    .features{
        display: flex;
        color: white;
        .lg(margin-bottom, 30px);
        .features_img{
            min-width: 35px;
            margin-right: 15px;
        }
        h4{
            font-size: 20px;
            font-weight: 700;
            margin-bottom: 20px;
            color: white;
            .sm(margin-bottom, 10px);
        }
        p{
            font-size: 10px;
            line-height: 18px;
        }
    }
    .twin{
        position: absolute;
        top: 0;
        left: 53%;
        right: 0;
        bottom: 0;
        width: 47%;
        background: url(../img/dr.png);
        z-index: -1;
        animation: star 40s infinite linear;
        .lg-block({
            left: 29%;
            width: 71%;
        });
        .md-block({
            left: 0;
            width: 100%;
        })
    }
    @keyframes star {
        from{
            background-position: 0 0;
        }
        to{
            background-position: 2000px 1000px;
        }
    }
}