@import "./params/mix";
@import "./params/var";
@import "header";
@import "feature";
@import "mobile";
@import "works";


body{
    font-family: 'Lato';
    min-width: 320px;
}
img{
    max-width: 100%;
    height: auto;
}
h2{
    color: @blue1;
    font-size: 21px;
    font-weight: 900;
    text-transform: uppercase;
    text-align: center;
}
h4{
    font-weight: 700;
    color: @dark;
    font-size: 13px;
}
.fixed-top{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
}