.mobile{
    .bg('../img/StarWarsSec.jpg');
    padding-top: 100px;
    padding-bottom: 50px;
    font-family: 'Open Sans';
    color: @dark;
    position: relative;
    .md-block({
        padding-top: 70px;
        padding-bottom: 20px;
    });
    .star_img{
        position: relative;
        .lg(display, none);
        img{
            position: absolute;
            left: 50%;
            margin-left: -132px;
            opacity: 0;
        }
        span{
            display: block;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: white;
            position: absolute;
            bottom: 90px;
            left: 50%;
            margin-left: -18px;
            padding-top: 10px;
            cursor: pointer;
            user-select: none;
        }
    }
    .mobile_title{
        margin-bottom: 70px;
        .md(margin-bottom, 30px);
        p{
            font-size: 15px;
            line-height: 30px;
            text-align: center;
        }
    }
    .mobile_box{
        display: flex;
        margin: 30px 0 70px;
        transition: .3s;
        padding: 14px;
        border-radius: 5px;
        .md(margin, 20px 0);
        &:hover{
            background-color: rgba(247, 227, 39, .5);
            cursor: pointer;
        }
        .mobile_img{
            min-width: 30px;
            margin-right: 35px;
        }
        h4{
            font-size: 18px;
            font-weight: 700;
            margin-bottom: 20px;
        }
        p{
            line-height: 20px;
            font-size: 12px;
        }
    }
    #scene1,
    #scene2,
    #scene3,
    #scene4{
        position: absolute;
        img{
            width: 200px;
            height: auto;
        }
    }
    #scene1{
        top: 100px;
        left: 100px;
    }
    #scene2{
        bottom: 100px;
        right: 100px;
    }
    #scene3{
        top: 100px;
        right: 100px;
    }
    #scene4{
        bottom: 100px;
        left: 100px;
    }
}